@use '../base/mixins' as m;
@use '../base/variables' as v;

.olvide {
    margin-top: 5rem;

    .uptask {
        @include m.texto-degradado(v.$rosa);
    }

    input[type="submit"] {
        @include m.boton(v.$rosa, v.$blanco);
    }

    .acciones {
        a {
            color: v.$rosa;
        }
    }
}